.caixa-pequena {
    width: 33vw;
    height: auto;
    min-height: 390px;
    background-color: white;
    position: relative;
    margin-right: 2vw;
    margin-bottom: 2vh;
    outline: 2px solid rgb(9, 6, 37);
    outline-offset: -10px;
    overflow-x: hidden;
}

.caixa-pequena p {
    padding: 20px;
    text-align: justify;
    font-family: 'Cookie', cursive;
    font-size: 31px;
    color: rgb(9, 6, 37);

}

.alinha {
    display: inline-flex;
}

@media (max-width: 600px) {
    .caixa-pequena {
        width: 98vw;
        margin-left: 1vw;
        margin-bottom: 1vh;
        min-height: 400px;
    }

    .caixa-pequena p {
        font-size: 26px;
    }

    .alinha {
        display:block;
    }
}

@media (max-height: 450px) {
    .caixa-pequena {
        width: 48vw;
        margin-left: 1vw;
        margin-right: 1vw;
        min-height: 400px;
    }

    .caixa-pequena p {
        font-size: 26px;
    }
}