@import url('https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:ital,wght@0,300;0,700;1,300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cookie&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Mrs+Saint+Delafield&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@100;300;400;500;700&display=swap');


* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

a {
    text-decoration: none;
    cursor: pointer;
}

body {
    font: 300, 14px, 'Noto Sans TC', sans-serif;
    background-color: rgb(9, 6, 37); 
    -webkit-font-smoothing: antialiased;
}

.fundo {
    background-image: url('./itens/universo4.jpg');
    background-attachment: fixed;
    background-size: cover;
    position: absolute;
    z-index: -4;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: .8;
}

.fundo h1 {
    font-family: 'Noto Sans TC', sans-serif;
    font-weight: 100;
    font-size: 40px;
    text-shadow: 0.1em 0.1em 0.2em black;
    color: white;
    text-align: center;
    margin-top:40vh; 
    margin-left:25vw;
}

@media (max-width: 600px), (max-height: 450px) {
    .fundo {
        display: none
    }

}

.fundo svg {
    font-size: 70px;
    rotate: 180deg;
}

button {
    cursor: pointer;
}

.menu {
    background-color: transparent;
    width: 100vw;
    height: 12vh;
    position: fixed;
    top: 0;
    font-family: 'Noto Sans TC', sans-serif;
    font-weight: 400;

    transition: all .5s ease;
}

.menu-scrolled {
    width: 100vw;
    height: 12vh;
    position: fixed;
    top: 0;
    font-family: 'Noto Sans TC', sans-serif;
    font-weight: 400;

    background-color: rgb(0, 0, 0, .8);
    transition: all .5s ease;

}

.menu a {
    color: white;
    font-size: 20px;

    text-shadow: 0.1em 0.1em 0.2em black;
    transition: all .5s ease;
}

.menu-scrolled a {
    color: white;

    font-size: 19px;
    text-shadow: 0.1em 0.1em 0.2em black;
    transition: all .5s ease;
}

.menu a::after {
    display: block;
    content: '';
    border-bottom: solid 3px rgb(136, 171, 247);
    padding-bottom: 3px;
    transform: scaleX(0);
    transition: transform 0.8s ease-in-out;
}

.menu-scrolled a::after {
    display: block;
    content: '';
    border-bottom: solid 3px white;
    padding-bottom: 3px;
    transform: scaleX(0);
    transition: transform 0.8s ease-in-out;
}

.menu-scrolled a:hover::after, .menu a:hover::after {
    transform: scaleX(1);
}


.menu ul, .menu-scrolled ul {
    list-style:none;
    float: right;
    padding: 2%;
    margin-right: 2%;
}

.menu li {
    float: left;
    padding: 2.2vh 2.5vh;
}

.menu-scrolled li {
    float: left;
    padding: 0vh 2.2vh;
}

.menu-logo {
    display: none;
}

.menu h1, .menu-scrolled h1 {
    display: none;
}

@media (max-width: 600px) {
    .menu, .menu-scrolled {
        position: fixed;
        background-color: rgb(0, 0, 0, .7);
        height: 12vh;
        z-index: 5;
    }

    .menu li, .menu-scrolled li {
        padding: none;
    }

    .menu ul, .menu-scrolled ul {
        padding: none;
    }

    .menu a, .menu-scrolled a {
        padding: none;
        font-size: 30px;
        
    }

    .hideMenu {
        display: none;
    }

    .showMenu {
        background-image: url('./itens/universo4.jpg');
        background-attachment: scroll;
        background-size: cover;
        background-position: center;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100vw;
        height: 100vh;
        z-index: -1;
        padding-top: 11vh;
    }

    .showMenu li {
        float: none;
        text-align: center;
        text-transform: uppercase;
        padding: 10px;
        font-weight: 100;
    }

    .showMenu a {
        text-align: center;
    }

    .showMenu a svg {
        display: none;
    }

    .showMenu svg {
        font-size: 50px;
        rotate: 180deg;
    }

    .showMenu h1 {
        display: block;
        font-family: 'Noto Sans TC', sans-serif;
        font-weight: 100;
        text-shadow: 0.1em 0.1em 0.2em black;
        color: white;
        text-align: center;
        margin-top:10vh; 

    }

    .menu-logo svg {
        font-size: 60px;
        color: white;
        padding-top: 2vh;

    }

    .menu-logo {
        display: block;
    }

    .menu-logo button {
        background-color: transparent;
        border: none;
    }

}

@media (max-height: 450px) {
    .menu, .menu-scrolled {
        position: fixed;
        background-color: rgb(0, 0, 0, .7);
        height: 25vh;
        z-index: 5;
    }

    .menu li, .menu-scrolled li {
        padding: none;
    }

    .menu ul, .menu-scrolled ul {
        padding: none;
    }

    .menu a, .menu-scrolled a {
        padding: none;
        font-size: 30px;
        
    }

    .hideMenu {
        display: none;
    }

    .showMenu {
        background-image: url('./itens/universo4.jpg');
        background-attachment: scroll;
        background-size: cover;
        background-position: center;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100vw;
        height: 100vh;
        z-index: -1;
        padding-top: 11vh;
        overflow: scroll;
    }

    .showMenu li {
        float: none;
        text-align: center;
        text-transform: uppercase;
        padding: 10px;
        font-weight: 100;
    }

    .showMenu a {
        text-align: center;
    }

    .showMenu a svg {
        display: none;
    }

    .showMenu svg {
        font-size: 50px;
        rotate: 180deg;
    }

    .showMenu h1 {
        display: block;
        font-family: 'Noto Sans TC', sans-serif;
        font-weight: 100;
        text-shadow: 0.1em 0.1em 0.2em black;
        color: white;
        text-align: center;
        margin-top:10vh; 

    }

    .menu-logo svg {
        font-size: 60px;
        color: white;
        padding-top: 2vh;

    }

    .menu-logo {
        display: block;
    }

    .menu-logo button {
        background-color: transparent;
        border: none;
    }

}

.caixa-sobre {
    background-color: rgb(9, 6, 37);
    width: 25vw;
    height: auto;
    margin-left: 2vw;
    position: fixed;
    box-shadow: -7px 7px 5px rgba(128, 128, 128, 0.349);
    margin-top: 12vh;
    top: 0;
    z-index: 1;
    border-right: black 1px solid;
}

@media (max-width: 600px), (max-height: 450px) {
    .caixa-sobre {
        width: 100vw;
        box-shadow: none;
        position: absolute;
        border: none;
        margin-left: 0vw;
        margin-top: 20vh;
    }
}

.caixa-sobre img {
    width: 60%;
    height: auto;
    border: 10px white solid;
    box-shadow: -7px 7px 5px rgba(128, 128, 128, 0.349);
    margin-left: 20%;
    margin-top: 5%;
}

.caixa-sobre h1 {
    font-family: 'Mrs Saint Delafield', cursive;
    font-size: 60px;
    color: white;
    text-align: center;
    margin-top: 5%;
}

.icons {
    color:white;
    text-align: center;
    font-size: 25px;
}

.icons svg {
    margin: 3.2vh;
}

.icons a {
    color: white;
    transition: all 0.7s;
}

.icons a:hover {
    color: #FF7FED;
    transition: all 0.5s;
}

@media (max-height: 450px) {
    .caixa-sobre h1 {
        font-size: 70px;
    }

    .icons {
        font-size: 50px;
    }

    .icons svg {
        margin: 6vh;
    }
}

.caixa-conteudo {
    width: 68vw;
    height: auto;
    background: transparent;
    margin-top: 100vh;
    margin-left: 29vw;
    position: relative;
    z-index: -1;
}

@media (max-width: 600px) {
    .caixa-conteudo {
        margin-left: 0vw;
        width: 100vw;
        margin-top: 100vh;
    }
}

@media (max-height: 450px) {
    .caixa-conteudo {
        margin-left: 0vw;
        width: 100vw;
        margin-top: 260vh;
        position: relative;
    }
}

.caixa-conteudo h1 {
    font-family: 'Noto Sans TC', sans-serif;
    font-weight: 300;
    font-size: 45px;
    text-shadow: 0.1em 0.1em 0.2em black;
    color: white;
    text-align: center;
    margin-bottom: 10vh;  
    text-decoration: underline;
    text-underline-position: under;  
    text-decoration-color: rgb(136, 171, 247);

}

#um {
    margin-bottom: 60px;
    color:rgb(9, 6, 37);
}

#dois {
    margin-bottom: 60px;
    color:rgb(9, 6, 37);
}


@media (max-width: 600px), (max-height: 450px) {
    #um {
        margin-bottom: 0px;
    }
    
    #dois {
        margin-bottom: 0px;
    }

}

footer {
    width: 100%;
    height: 20vh;
    background-color: black;
    bottom: 0;
    position: relative;
    color: white;
    text-align: right;
    padding: 60px;
    font-size: 20px;
    font-weight: 300;
    font-family: 'Noto Sans TC', sans-serif;

}

footer svg {
    color: rgb(212, 107, 9);
}

@media (max-width: 600px), (max-height: 450px) {
    footer {
        text-align: left;
    }
}

.faixa {
    background-image: url('./itens/universo.jpg');
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    height: 30vh;

}

@media (max-width: 600px) {
    .faixa {
        height: 10vh;
    }
}

@media (max-height: 450px) {
    .faixa {
        height: 20vh;
    }
} 

form {
    width: 68vw;
    height: auto;
    background: transparent;
    margin-left: 29vw;
    z-index: -1;
}

form h1 {
    font-family: 'Noto Sans TC', sans-serif;
    font-weight: 300;
    font-size: 45px;
    text-shadow: 0.1em 0.1em 0.2em black;
    color: white;
    text-align: center;
    margin-bottom: 10vh;  
    text-decoration: underline;
    text-underline-position: under;  
    text-decoration-color: rgb(212, 107, 9);

}

form input {
    width: 32.5vw;
    margin-left: 1vw;
    padding: 20px;
    font-size: 20px;
    border: white solid 2px;
}

form textarea {
    width: 66vw;
    max-width: 66vw;
    height: 30vh;
    margin-left: 1vw; 
    margin-top: 2vh;
    margin-bottom: 2vh;
    padding: 20px;
    font-size: 20px;
}

form input:focus, form textarea:focus {
    border: rgb(212, 107, 9) solid 3px;
}


form button {
    width: 15vw;
    margin-bottom: 10vh;
    margin-left: 26.5vw;
    padding: 15px;
    border: 0;
    border-radius: 3px;
    font-size: 20px;
    background: rgb(212, 107, 9);
    color: white;
    
}

form button:hover {
    background: rgba(212, 107, 9, 0.9);
}

#contato {
    margin-bottom: 60px;
    color:rgb(9, 6, 37);
}

@media (max-width: 600px) {
    form {
        width: 100vw;
        margin-left: 0vw;
    }

    form h1 {
        margin-bottom: 5vh; 
    }

    form input {
        width: 98vw;
        margin-bottom: 1vh;
    }

    form textarea {
        width: 98vw;
        max-width: 98vw;
        margin-top: 0vh;
        height: 20vh;
    }

    form button {
        width: 50vw;
        margin-left: 25vw;
        margin-bottom: 5vh;
    }

    #contato {
        margin-bottom: 0px;
    }
}

@media (max-height: 400px) {
    form {
        width: 100vw;
        margin-left: 0vw;
    }

    form h1 {
        margin-bottom: 5vh; 
    }

    form input {
        width: 98vw;
        margin-bottom: 2vh;
    }

    form textarea {
        width: 98vw;
        max-width: 98vw;
        margin-top: 0vh;
        height: 40vh;
    }

    form button {
        width: 50vw;
        margin-left: 25vw;
        margin-bottom: 5vh;
    }

    #contato {
        margin-bottom: 0px;
    }
}